<script setup>
import { ref, reactive, toRefs, onBeforeMount } from 'vue';
import { swapStore } from '@/store/swap';
import pixelInfo from '@/components/pixelInfo.vue';
import { MaxUint256 } from '@ethersproject/constants';

import { Toast } from 'vant';
import { commonStore } from '@/store/common';
import { getXY } from '@/utils';
const swap_store = swapStore();
const common_store = commonStore();
const props = defineProps({
  info: Object,
  colorId: Number,
});
defineEmits(['buy']);
const { info } = toRefs(props);
const data = reactive({
  x: 0,
  y: 0,
});

onBeforeMount(() => {
  data.x = getXY(info.value.id).x;
  data.y = getXY(info.value.id).y;
});
const empower = async () => {
  if (!swap_store.tokenEmpower) {
    await swap_store.getTokenMethods();
  }
  // common_store.setEmpowerType(5, info.value.tokenId);
  // common_store.showEmpower(true);
  const tokenid = info.value?.tokenId || info.value?.tokenid;
  try {
    const res = await swap_store.tokenMethods
      .approve(swap_store.marketSpender, tokenid)
      .send({ from: common_store.address });
    swap_store.setMarketEmpower();
  } catch (error) {
    Toast(error);
  }
};
</script>
<template>
  <ul class="buy-info bg">
    <li class="titleBg padding allFlex li_info">
      <pixel-info
        :x="data.x"
        :y="data.y"
        :price="info.price"
        :colorId="colorId"
      ></pixel-info>
    </li>
    <!-- <li v-if="!swap_store.marketEmpower" class="li_btn">
      <van-button type="default" block @click="empower">授权SkyT</van-button>
    </li> -->
    <li class="li_btn">
      <van-button type="primary" block @click="$emit('buy')">{{
        $t('buy')
      }}</van-button>
    </li>
  </ul>
</template>

