<script setup>
import { ref, reactive, onBeforeMount, watchEffect } from 'vue';
import { useRoute, useRouter } from 'vue-router';
import { commonStore } from '@/store/common';
import { swapStore } from '@/store/swap';
import buyInfo from './components/buyInfo.vue';
import swapAlert from '@/components/swapAlert.vue';
import { getPixelRecord } from '@/http/api';
import { MaxUint256 } from '@ethersproject/constants';
import { Toast, Notify } from 'vant';
import { getFullTime, getUrl, balanceUtil } from '@/utils/index.js';
import { useI18n } from 'vue-i18n';

const route = useRoute();
const router = useRouter();
const common_store = commonStore();
const swap_store = swapStore();
const { t } = useI18n();
const info = route.query;
const tokenid = info?.tokenid || info.tokenId;
const navArr = ['price', 'Market.nav.four', 'Market.nav.five'];
let colorId = ref(0);
const data = reactive({
  list: [],
  alertShow: false,
  finish: false,
  success: false,
  hash: '',
  loading: true,
  finished: false,
  page: 0,
  show: false,
  check: false,
  empowerText: 'EmpowerAlert.btn4',
  total: 0,
  tokenBal: '',
});

const getPixelColor = async () => {
  const res = await common_store.allMethods.getPixel(tokenid).call();
  colorId.value = res.color - 1;
  console.log(res);
};
const getTokenBal = async () => {
  await swap_store.getTokenMethods();
  const res = await swap_store.tokenMethods
    .balanceOf(common_store.address)
    .call();
  data.tokenBal = balanceUtil(res + '');
};
const getRecordList = () => {
  getPixelRecord(tokenid, data.page).then((res) => {
    data.loading = false;
    const list = res.data.results;
    data.list = data.list.concat(list);
    data.finished = list.length < 20;
  });
};
onBeforeMount(() => {
  getPixelColor();
  getRecordList();
});
watchEffect(() => {
  if (common_store.address) {
    getTokenBal();
  }
});
const onLoad = () => {
  if (data.finished) {
    return;
  }
  data.loading = true;
  data.page++;
  getRecordList();
};

const toBuy = async () => {
  if (info.seller == common_store.address) {
    Toast(t('Market.alert'));
    return;
  }

  if (+data.tokenBal < +balanceUtil(info.price)) {
    Notify({
      type: 'warning',
      message: t('skytBal'),
      background: '#3761ea',
      color: '#fff',
      duration: 1500,
    });
    return;
  }
  data.alertShow = true;
  data.finish = false;
  data.success = false;
};
const buyFn = async () => {
  if (data.check) {
    return;
  }
  data.check = true;
  try {
    const res = await common_store.marketMethods
      .buy(info.id)
      .send({ from: common_store.address });
    data.finish = true;
    data.success = true;
    data.check = false;
    data.hash = res.transactionHash;
    common_store.addHash(data.hash, 0);
  } catch (error) {
    data.finish = true;
    data.success = false;
    data.check = false;
    data.hash = error.message;
  }
};
const empowerFn = async () => {
  if (data.check) {
    return;
  }
  data.empowerText = 'EmpowerAlert.btn1';
  data.check = true;
  try {
    const res = await swap_store.tokenMethods
      .approve(swap_store.marketSpender, MaxUint256)
      .send({ from: common_store.address });
    swap_store.setMarketEmpower();
    console.log(res);
    data.check = false;
    data.empowerText = 'EmpowerAlert.btn4';
  } catch (error) {
    data.check = false;
    data.empowerText = 'EmpowerAlert.btn4';
    Toast(error.message);
  }
};
const closeFn = () => {
  data.alertShow = false;
  if (data.success) {
    setTimeout(() => {
      router.go(-1);
    }, 1000);
  }
};
</script>
<template>
  <div class="details">
    <title-text></title-text>
    <p class="Wcolor font-Tt Bold details_title">
      {{ $t('name') }}#{{ tokenid }}
    </p>
    <img :src="getUrl(tokenid)" alt="" class="details_img" />
    <p class="font-Six Wcolor">
      {{ $t('Market.seller') }}
      <span class="lightGrey font-Six marginTen"
        >{{ info?.seller?.slice(0, 4) }}...{{ info?.seller?.slice(-4) }}</span
      >
    </p>
    <buy-info :info="info" :colorId="colorId" @buy="toBuy"></buy-info>

    <div
      class="record titleBg"
      :style="{ height: data.show ? '455px' : '70px' }"
    >
      <div
        class="flex_between record-title padding bg"
        @click="data.show = !data.show"
      >
        <div class="flex_start">
          <img
            src="@assets/marketImg/record_icon.png"
            alt=""
            class="record_icon"
          />
          <span class="Wcolor Bold marginTen">{{ $t('Market.record') }}</span>
        </div>
        <img
          src="@assets/accountImg/down_icon.png"
          alt=""
          class="down_icon"
          :class="[data.show ? 'up_icon' : 'down_icon']"
        />
      </div>
      <div class="record-bottom">
        <record-nav
          oneWidth="90px"
          endWidth="128px"
          :navArr="navArr"
        ></record-nav>
        <div class="record-main">
          <van-list
            v-model:loading="data.loading"
            :finished="data.finished"
            :immediate-check="false"
            :loading-text="$t('loadingText')"
            @load="onLoad"
          >
            <ul>
              <li
                class="flex_between list-slot Wcolor"
                v-for="(item, key) in data.list"
                :key="key"
              >
                <div class="flex_start font-Three" style="width: 85px">
                  <span class="Wcolor price">{{
                    Number(balanceUtil(item.price)).toFixed(2)
                  }}</span>
                </div>
                <span class="f1 center font-Three"
                  >{{ item.owner.slice(0, 6) }}...{{
                    item.owner.slice(-4)
                  }}</span
                >
                <span
                  style="width: 128px; text-align: right"
                  class="timeFont font-Three"
                  >{{ getFullTime(item.selltime) }}</span
                >
              </li>
            </ul>

            <no-record v-if="data.list.length == 0"></no-record>
          </van-list>
        </div>
      </div>
    </div>

    <van-popup v-model:show="data.alertShow">
      <swap-alert
        :finish="data.finish"
        :success="data.success"
        :hash="data.hash"
        :check="data.check"
        title="SwapAlert.title5"
        text="SwapAlert.success3"
        btn="buy"
        :empower="swap_store.marketEmpower"
        :empowerText="data.empowerText"
        @closeFn="closeFn"
        @empowerFn="empowerFn"
        @submit="buyFn"
      >
        <p class="font-Four">
          {{
            $t('Home.text12', {
              start: common_store.address.slice(0, 6),
              end: common_store.address.slice(-4),
            })
          }}
        </p>
      </swap-alert>
    </van-popup>
  </div>
</template>

