  width: 8px;
  height: 14px;
}
.price {
  margin: 0 5px 0 5px;
}
.list-slot {
  height: 40px;
}
.record {
  transition: ease-in-out 0.2s;
  overflow: hidden;
  &-main {
    max-height: 350px;
    overflow-y: auto;
  }
  border-radius: 12px;
  overflow: hidden;
}
.record-title {
  height: 70px;
}
.record_icon,
.down_icon {
  width: 25px;
  height: 25px;
}
.down_icon {
  transition: ease-in-out 0.2s;
  transform: rotate(180deg);
}
.up_icon {
  transition: ease-in-out 0.2s;
  transform: rotate(0);
}
.record-bottom {
  padding: 0 8px;
  border-top: 2px solid $border2;
}
</style>
